<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "cardMakeManagementComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      schedule_no: "",
      available_begin_date: "",
      class_date: "",
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "dDate",
          label: "報名日期/簡稱",
          width: "150",
        },
        {
          id: "nameId",
          label: "身份證字號/性別/姓名",
          width: "200",
        },
        {
          id: "approve_Status",
          label: "審查狀態",
        },
        {
          id: "attend_Status",
          label: "上課狀態",
        },
        {
          id: "issue_Card",
          label: "發卡狀態",
        },
      ],
      detail: {},
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    changePageHandler(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    async updCardStatus(item, val) {
      item.issue_Card = val;
      const res = await this.$store.dispatch("updCardStatus", item);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: 5000,
          showClose: "true",
          message: "修改成功",
        });
        this.qryQualifyDetail(
          this.schedule_no,
          this.available_begin_date,
          this.class_date
        );
        this.$store.state.currentPage = 1;
      }
    },
    async qryQualifyDetail(schedule_no, available_begin_date, class_date) {
      const res = await this.$store.dispatch("qryQualifyDetail", {
        schedule_no,
        available_begin_date,
        class_date,
      });
      const data = res.data.data;
      this.detail = data;
      if (data.course_Supplier || data.course_Supplier.length) {
        this.tableData = JSON.parse(JSON.stringify(data.course_Supplier));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.dDate = `${e.select_Date}/ ${e.short_Name}`;
          e.nameId = `${e.id}/${e.sex || ""}/${e.p_Name || ""}`;
          e.approveStatus = this.getApproveStatus(e.approve_Status);
        });
        //this.tableList = this.tableData.slice(0, 10);
        //審核拒絕的就不再秀在製卡畫面上了
        const filteredData = this.tableData.filter(
          (e) => e.approve_Status !== "2"
        );
        this.tableList = filteredData.slice(0, 10);
      }
    },
  },
  async mounted() {
    this.schedule_no = this.$route.params.schedule_no;
    this.available_begin_date = this.$route.params.available_begin_date;
    this.class_date = this.$route.params.class_date;
    this.qryQualifyDetail(
      this.schedule_no,
      this.available_begin_date,
      this.class_date
    );
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./cardMakeManagement.html"></template>
